import React, {useState, useEffect} from 'react'
import {Link} from 'react-router-dom'

export default function ComposerPage({name}) {
        const [queryName, setqueryName] = useState(name)
        const [wikiExtract, setWikiExtract] = useState([])
        const [wikiImageURL, setWikiImageURL] = useState([])
        const [wikiInfobox, setWikiInfobox] = useState([])
        const [wikiName, setWikiName] = useState([])

        async function getData(){
            const url = `https://en.wikipedia.org/w/api.php?action=query&format=json&prop=revisions%7Cpageimages%7Cextracts&generator=search&rvprop=content&rvslots=*&rvsection=0&piprop=thumbnail%7Cname&pithumbsize=500&gsrsearch=${queryName}&gsrlimit=1&gsrsort=relevance&origin=*`;

            const response = await fetch(url)
                const jsonRes = await response.json()
                const wikiExtract = Object.entries(jsonRes.query.pages)[0][1].extract
                const wikiImageURL = Object.entries(jsonRes.query.pages)[0][1].thumbnail.source
                const wikiName = Object.entries(jsonRes.query.pages)[0][1].title
        
            setWikiExtract(wikiExtract)
            setWikiImageURL(wikiImageURL)
            setWikiInfobox(wikiInfobox)
            setWikiName(wikiName)
            
            console.log(name)
        }

        useEffect(() => {
            getData();
        }, []);
  

    return (
            <div className="composer-page-container"  >
                <Link to="/">
                    <svg id ="backArrow" aria-hidden="true" focusable="false" data-prefix="fas" data-icon="arrow-left" className="svg-inline--fa fa-arrow-left fa-w-14" role="img" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 448 512"><path fill="rgb(219, 216, 216)" d="M257.5 445.1l-22.2 22.2c-9.4 9.4-24.6 9.4-33.9 0L7 273c-9.4-9.4-9.4-24.6 0-33.9L201.4 44.7c9.4-9.4 24.6-9.4 33.9 0l22.2 22.2c9.5 9.5 9.3 25-.4 34.3L136.6 216H424c13.3 0 24 10.7 24 24v32c0 13.3-10.7 24-24 24H136.6l120.5 114.8c9.8 9.3 10 24.8.4 34.3z"></path></svg>
                </Link>
                <div className="wikiExtract">
                    <div className="wikiPage-widget">
                        <img src={wikiImageURL} />
                        <h2 id="wikiName">{wikiName}</h2>
                        <p dangerouslySetInnerHTML={{__html: wikiExtract}}></p>
                    </div>

                    <div className="music-player-container">
                                <h3>Take a listen.</h3>
                                <p>Example</p>
                                <p>Example</p>
                                <p>Example</p>
                            </div>
                </div> 
            </div>
    )
}
